	module.exports = {"data":{"markdownRemark":{"html":"<h2>Customer Journey Maps</h2>\n<h3>What Is Customer journey map?</h3>\n<p>Customer journey mapping is a tool to map out the entire user story as a whole - from the initial contact, usage until the abandonment of the service. </p>\n<h3>How Does It Look Like?</h3>\n<p>The customer journey maps can have many forms and usually consist of a description of user’s needs, feelings, motivations, touchpoints (eg. e-mail, customer care phone line, various devices etc.), interactions and the team members responsible for solution within those interactions. </p>\n<p><img src=\"https://www.dragon1.com/images/customer-journey-mapping-2.png\" alt=\"Example of customer journey map\"></p>\n<h3>How To Create One?</h3>\n<p>The building of such map starts with thorough user research - both quantitative and qualitative and then maps it to the desired outcomes of the service. </p>\n<h3>Why Do We Need It?</h3>\n<p>It helps the team building or maintaining a service, application or website to see the context of the whole experience in one place. It also helps to identify weak points and opportunities of the process. The main reason for creating such map is to stay focused on the user and not on technical, organisational or other aspects of building the service.</p>\n<h3>More Information On Creating Customer Journey Map:</h3>\n<p><a href=\"https://uxmastery.com/how-to-create-a-customer-journey-map/\">How To Create A Customer Journey Map</a></p>","fields":{"slug":"/user-experience-research/customer-journey-maps/"},"frontmatter":{"title":"Customer Journey Maps"}}},"pathContext":{"slug":"/user-experience-research/customer-journey-maps/"}}

